import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import GridLayout from 'react-grid-layout';
import 'react-grid-layout/css/styles.css';
import 'react-resizable/css/styles.css';
import { actions as appActions } from '../reducers/app';
import Widget from '../components/Widget';
import Link from '../components/Link';
import * as Colors from '../constants/Colors';
import iconList from '../assets/iconList.svg';
import iconCreate from '../assets/iconCreate.svg';
import iconEdit from '../assets/iconEdit.svg';
import iconAdd from '../assets/iconAdd.svg';

const styles = {
  container: {
    width: 1000,
  },
  link: {
    marginBottom: 10,
    color: Colors.darkBlue,
  },
};

class Dashboard extends Component {
  componentDidMount() {
    document.title = 'StundenPlaner - Dashboard';
  }

  render() {
    if (!this.props.isLoggedIn) {
      this.props.history.push('/');
      return null;
    }

    return (
      <GridLayout style={styles.container} width={1200} cols={3} rowHeight={20} margin={[10, 10]}>
        <div key="Stundenplne" data-grid={{ x: 0, y: 0, w: 1, h: 6 }}>
          <Widget style={styles.widget} text="Stundenplne">
            <Link
              icon={iconList}
              text="Stundenplanliste anzeigen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Stundenplne',
                  },
                });
              }}
            />
            <Link
              icon={iconCreate}
              text="Stundenplan erstellen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Stundenplne',
                  },
                });
              }}
            />
            <Link
              icon={iconEdit}
              text="Stundenplan bearbeiten"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Stundenplne',
                  },
                });
              }}
            />
          </Widget>
        </div>
        <div key="Klassen" data-grid={{ x: 0, y: 0, w: 1, h: 5 }}>
          <Widget style={styles.widget} text="Klassen">
            <Link
              icon={iconList}
              text="Klassenliste anzeigen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Klassen',
                  },
                });
              }}
            />
            <Link
              icon={iconCreate}
              text="Klasse erstellen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Klassen',
                  },
                });
              }}
            />
          </Widget>
        </div>
        <div key="Fcher" data-grid={{ x: 0, y: 0, w: 1, h: 5 }}>
          <Widget style={styles.widget} text="Fcher">
            <Link
              icon={iconList}
              text="Fcherliste anzeigen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Fcher',
                  },
                });
              }}
            />
            <Link
              icon={iconCreate}
              text="Fach erstellen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Fcher',
                  },
                });
              }}
            />
          </Widget>
        </div>
        <div key="Accounts" data-grid={{ x: 0, y: 0, w: 1, h: 6 }}>
          <Widget style={styles.widget} text="Accounts">
            <Link
              icon={iconList}
              text="Accountliste anzeigen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Accounts',
                  },
                });
              }}
            />
            <Link
              icon={iconAdd}
              text="Account erstellen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Accounts',
                  },
                });
              }}
            />
            <Link
              icon={iconAdd}
              text="Ausloggen"
              onClick={() => {
                this.props.logout();
                this.props.history.push('/');
              }}
            />
          </Widget>
        </div>
        <div key="Schler" data-grid={{ x: 1, y: 0, w: 1, h: 7 }}>
          <Widget style={styles.widget} text="Schler">
            <Link
              icon={iconList}
              text="Schlerliste anzeigen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Schler',
                  },
                });
              }}
            />
            <Link
              icon={iconAdd}
              text="Schler erstellen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Schler',
                  },
                });
              }}
            />
          </Widget>
        </div>
        <div key="Lehrer" data-grid={{ x: 1, y: 0, w: 1, h: 7 }}>
          <Widget style={styles.widget} text="Lehrer">
            <Link
              icon={iconList}
              text="Lehrerliste anzeigen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Lehrer',
                  },
                });
              }}
            />
            <Link
              icon={iconAdd}
              text="Lehrer erstellen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Lehrer',
                  },
                });
              }}
            />
          </Widget>
        </div>
        <div key="Rume" data-grid={{ x: 1, y: 0, w: 1, h: 7 }}>
          <Widget style={styles.widget} text="Rume">
            <Link
              icon={iconList}
              text="Raumliste anzeigen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Rume',
                  },
                });
              }}
            />
            <Link
              icon={iconCreate}
              text="Raum erstellen"
              onClick={() => {
                this.props.history.push({
                  pathname: '/details',
                  state: {
                    title: 'Rume',
                  },
                });
              }}
            />
          </Widget>
        </div>
        <div key="Kalender" data-grid={{ x: 2, y: 0, w: 1, h: 15 }}>
          <Widget style={styles.widget} text="Kalender" />
        </div>
        <div key="Wetter" data-grid={{ x: 2, y: 2, w: 1, h: 6 }}>
          <Widget style={styles.widget} text="Wetter" />
        </div>
      </GridLayout>
    );
  }
}

const mapStateToProps = state => ({
  token: state.app.token,
  profile: state.app.profile,
  isLoggedIn: state.app.isLoggedIn,
});

const mapDispatchToProps = {
  logout: appActions.logout,
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Dashboard));



